import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {FaBars,MdClose} from "react-icons/all"
class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {activeClass: '',
            mobileMenu:false,
            mobileMenuCloseActive:''
        };
    }
    componentDidMount(){
        window.addEventListener('scroll', () => {
            if(window.scrollY > 5){
                this.setState({activeClass: 'menu--active',mobileMenuCloseActive:'mobile-menu--active'});
            }
            else {
                this.setState({activeClass: '',mobileMenuCloseActive:''});
            }
        });
    }
    mobileMenuToggle=()=>{
        if(this.state.mobileMenu){
            this.setState({mobileMenu:false})
        }
        else {
            this.setState({mobileMenu:true})
        }
    }
    srollToSection=(event)=>{
        var target = event.target.getAttribute('href');
        console.log(window.document.getElementsByClassName('resume')[0].offsetTop);
        window.scrollTo({top:window.document.getElementsByClassName(target.split("#")[1])[0].offsetTop-50,behavior: 'smooth'})
    }
    mobileMenuRemove=(event)=>{
        this.setState({mobileMenu:false})
        this.srollToSection(event);
    }

    render() {
        return (
            <>
            <div className={`menu ${this.state.activeClass}`}>
                <div className="container">
                    <div className="row">
                        <div className="menu__wrapper d-none d-lg-block col-md-12">
                            <nav className="">
                                <ul>
                                    <li><a href="#about" onClick={this.mobileMenuRemove}>About</a></li>
                                    <li><a href="#resume" onClick={this.mobileMenuRemove}>Resume</a></li>
                                    <li><a href="#portfolio" onClick={this.mobileMenuRemove}>Portfolio</a></li>
                                    <li><a href="#contact" onClick={this.mobileMenuRemove}>Contact</a></li>
                                </ul>
                            </nav>
                        </div>
                        <div className="menu__wrapper col-md-12 d-lg-none">
                            <button type="button" className="menu__mobile-button" style={{padding:0}}>
                                <span onClick={this.mobileMenuToggle}><FaBars/></span>
                            </button>
                        </div>
                    </div>

                </div>
            </div>
                <div className={`${this.state.mobileMenu ? "mobile-menu d-lg-none active "+this.state.mobileMenuCloseActive : "mobile-menu d-lg-none "+this.state.mobileMenuCloseActive}`}>
                    <div className="container">
                        <div className="mobile-menu__close" onClick={this.mobileMenuToggle}>
                            <span><MdClose/></span>
                        </div>
                        <nav className="mobile-menu__wrapper">
                            <ul>
                                <li><a onClick={this.mobileMenuRemove} href="#about">About</a></li>
                                <li><a onClick={this.mobileMenuRemove} href="#resume">Resume</a></li>
                                <li><a onClick={this.mobileMenuRemove} href="#portfolio">Portfolio</a></li>
                                <li><a onClick={this.mobileMenuRemove} href="#contact">Contact</a></li>
                            </ul>
                        </nav>
                    </div>
                </div>
                </>
        );
    }
}

export default Header;